.price {
  background-color: #ffffff;
  text-align: center;
  padding-top: 95px;
  padding-bottom: 70px;

  & > h2 {
    word-break: break-word;
    max-width: 330px;
    margin: 0 auto;
    font-size: $font-size-h2;
    font-weight: 800;
    line-height: 22px;
    margin-bottom: 7px;
    letter-spacing: -0.6px;
    color: $color-title;
  }

  & > p {
    max-width: 600px;
    margin: 0 auto;
    line-height: 30px;
    position: relative;
    margin-bottom: 50px;
    color: $color-title;

    &::after {
      content: "";
      position: absolute;
      width: 40px;
      height: 2px;
      bottom: -11px;
      left: 50%;
      transform: translateX(-50%);
      background-color: #525961;
    }
  }
}

.price__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (max-width: $mobile-width) {
  .price {
    padding-top: 45px;

    & > p {
      padding-left: 17px;
      padding-right: 17px;
    }
  }
}
