.contacts {
  width: 50%;
  background-color: #5e4f4b;
  background-image: url("../img/overlay_form.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  box-sizing: border-box;
  padding-top: 113px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 100px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/overlay_form@2x.webp");
  }
}

@media (max-width: $tablet-width) {
  .contacts {
    width: 100%;
    padding-top: 45px;
    padding-bottom: 40px;
  }
}

@media (max-width: $mobile-width) {
  .contacts {
    width: 100%;
    padding-right: 17px;
    padding-left: 17px;
  }
}
